import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { createMuiTheme } from "@material-ui/core/styles";
import { AppBar, SwipeableDrawer, Typography } from "@material-ui/core";
import { Box, ThemeProvider, useScrollTrigger } from "@material-ui/core";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import IconButton from "@material-ui/core/IconButton";
import Toolbar from "@material-ui/core/Toolbar";
import Avatar from "@material-ui/core/Avatar";
import Slide from "@material-ui/core/Slide";
import avatar from "../images/avatar-1.jpg";
import Sidebar from "./Sidebar";

const theme = createMuiTheme({
  shadows: ["none"],
  palette: {
    primary: {
      main: "#1DA1F2",
    },
    secondary: {
      main: "#14171A",
    },
  },
});

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  toolbar: {
    justify: "space-between",
    borderBottom: "1px solid #c1c1c1",
  },
  menuButton: {
    margin: theme.spacing(1, 0, 1),
    padding: theme.spacing(0),
  },
  title: {
    flexGrow: 1,
  },
  twLogo: {
    height: 22,
  },
  list: {
    width: 275,
  },
  listItems: {
    margin: theme.spacing(0),
  },
}));

// Hide top navbar on scroll
function HideOnScroll(props) {
  const { children } = props;
  const trigger = useScrollTrigger();
  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

function Header(props) {
  const classes = useStyles();

  const [state, setState] = React.useState({
    drawerOpen: false,
  });

  const toggleDrawer = (open) => (event) => {
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }
    setState({ ...state, drawerOpen: open });
  };

  // Items inside sidebar
  // apply toggler
  const list = () => {
    return (
      <div
        className={classes.list}
        role="presentation"
        onClick={toggleDrawer(false)}
        onKeyDown={toggleDrawer(false)}
      >
        <Sidebar />
      </div>
    );
  };

  return (
    <ThemeProvider theme={theme}>
      <div className={classes.root}>
        <HideOnScroll {...props}>
          <AppBar position="fixed" color="inherit" elevation={0}>
            <Toolbar variant="dense" className={classes.toolbar}>
              <React.Fragment key={state.drawerOpen}>
                <IconButton
                  edge="start"
                  className={classes.menuButton}
                  color="inherit"
                  aria-label="menu"
                  onClick={toggleDrawer(true)}
                >
                  <Avatar
                    alt="Chamberlain Francis"
                    src={avatar}
                    style={{ height: 35, width: 35 }}
                  />
                </IconButton>

                <SwipeableDrawer
                  anchor="left"
                  open={state.drawerOpen}
                  onClose={toggleDrawer(false)}
                  onOpen={toggleDrawer(true)}
                  elevation={0}
                >
                  {list()}
                </SwipeableDrawer>
              </React.Fragment>

              <Typography
                variant="h6"
                color="inherit"
                className={classes.title}
              >
                <Box fontWeight="fontWeightBold" ml={3}>
                  Home
                </Box>
              </Typography>

              <IconButton color="inherit" className={classes.menuButton}>
                <MoreVertIcon color="primary" />
              </IconButton>
            </Toolbar>
          </AppBar>
        </HideOnScroll>
      </div>
    </ThemeProvider>
  );
}

export default Header;
