import React from "react";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";

import InboxIcon from "@material-ui/icons/MoveToInbox";
import MailIcon from "@material-ui/icons/Mail";
import Slide from "@material-ui/core/Slide";
import Avatar from "@material-ui/core/Avatar";
import CloseIcon from "@material-ui/icons/Close";

import avatar from "../images/avatar-1.jpg";
import {
  Box,
  Container,
  Divider,
  Grid,
  IconButton,
  Typography,
} from "@material-ui/core";

function Sidebar() {
  return (
    <>
      <Container maxWidth="xl">
        <Grid container spacing={1} alignItems="center" justify="space-between">
          <Grid>
            <Typography variant="subtitle1" component="div">
              <Box component="p" mb={1} fontWeight="fontWeightBold">
                Account info
              </Box>
            </Typography>
          </Grid>
          <Grid>
            <IconButton color="primary" size="small">
              <CloseIcon />
            </IconButton>
          </Grid>
        </Grid>
      </Container>
      <Divider />

      <Container maxWidth="xl">
        <Box mt={1}>
          <Box>
            <Avatar alt="Chamberlain Francis" src={avatar} />
          </Box>
          <Typography component="div">
            <Box component="p" mt={1} mb={-0.5} fontWeight="fontWeightBold">
              Chamberlain
            </Box>
            <Box component="span" m={0}>
              @freesou_l
            </Box>
          </Typography>
        </Box>
      </Container>

      <Container maxWidth="xl">
        <Box display="flex" my={2}>
          <Typography component="div">
            <Box component="span" mr={3}>
              523 following
            </Box>
          </Typography>
          <Typography component="div">
            <Box component="span">110 followers</Box>
          </Typography>
        </Box>
      </Container>

      <List>
        {["Inbox", "Starred", "Send email", "Drafts"].map((text, index) => (
          <ListItem button key={text} dense>
            <ListItemIcon>
              {index % 2 === 0 ? (
                <InboxIcon fontSize="small" />
              ) : (
                <MailIcon fontSize="small" />
              )}
            </ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List>
      <Divider />
    </>
  );
}

export default Sidebar;
